import renderSearchString from '../view/View';
import fetchVideoItems from '../model/Model';
import PageControl from '../components/PageControl/PageControl';
import VideoClips from '../components/VideoClips/VideoClips';
import { getDisplayedClipsQuantity, getWindowWidth } from '../util';
import { CHUNK_SIZE } from '../consts/consts';

class App {
  constructor() {
    this.videos = [];
    this.search = this.search.bind(this);
    this.onResize = this.onResize.bind(this);
    this.onPrevClick = this.onPrevClick.bind(this);
    this.onNextClick = this.onNextClick.bind(this);
    this.onSearchApply = this.onSearchApply.bind(this);
    this.nextPageToken = null;
    this.getPageSize = () => getDisplayedClipsQuantity(getWindowWidth());
    this.fetchVideoItems = fetchVideoItems;
    this.offset = 0;
  }

  loadNextChunk() {
    if (this.videos.length - this.offset <= CHUNK_SIZE * 0.75) {
      const {
        searchQuery,
        nextPageToken,
      } = this;
      this.fetchVideoItems({ searchQuery, pageToken: nextPageToken })
        .then((videosChunk) => {
          const {
            items,
          } = videosChunk;
          this.nextPageToken = videosChunk.nextPageToken;
          this.videos = this.videos.concat(items);
        });
    }
  }

  updateView(isNext = true) {
    this.videoClips.updateClips(this.getClipsPage(), isNext);
    this.pageControl.updateCurPageNum(this.getCurPageNum());
  }

  onPrevClick() {
    let offset = this.offset - this.getPageSize();
    if (offset < 0) offset = 0;
    this.offset = offset;
    this.updateView(false);
  }

  onNextClick() {
    this.offset = this.offset + this.getPageSize();
    this.loadNextChunk();
    this.updateView();
  }

  getCurPageNum() {
    return Math.floor(this.offset / this.getPageSize());
  }

  getClipsPage() {
    return this.videos.slice(this.offset, this.offset + this.getPageSize());
  }

  onSearchApply() {
    const tmpElem = document.getElementsByClassName('page-control')[0];
    if (tmpElem) tmpElem.remove();
    this.videoClips.updateClips(this.getClipsPage());
    const pageControlElem = this.pageControl.render();
    pageControlElem.classList.add('page-control');
    document.body.appendChild(pageControlElem);
  }

  search(event) {
    this.offset = 0;
    this.nextPageToken = null;
    this.videos = [];

    const searchQuery = event.target.value;
    this.searchQuery = searchQuery;
    this.fetchVideoItems({ searchQuery })
      .then((videosChunk) => {
        const {
          items,
          nextPageToken,
        } = videosChunk;
        this.nextPageToken = nextPageToken;
        this.videos = items;
        return this.getClipsPage();
      })
      .then(this.onSearchApply);
  }

  onResize() {
    this.updateView();
  }

  start() {
    this.videoClips = new VideoClips(this.onPrevClick, this.onNextClick, this.getClipsPage());
    this.pageControl = new PageControl(this.onPrevClick, this.onNextClick, this.getCurPageNum());
    window.addEventListener('resize', this.onResize);
    renderSearchString(this.search);
    document.body.appendChild(this.videoClips.render());
  }
}

export default App;
