import {
  CHUNK_SIZE,
  WATCH_URL,
  VIDEOS_URL,
  SEARCH_URL,
} from '../consts/consts';

const fetchChunkInfo = ({ searchQuery, pageToken }) => fetch(`${SEARCH_URL}&part=id${pageToken ? `&pageToken=${pageToken}` : ''}&q=${searchQuery}&type=video&maxResults=${CHUNK_SIZE}`)
  .then(responce => responce.json())
  .then(({ nextPageToken, items }) => ({
    videoIds: items.map(item => item.id.videoId).join(','),
    nextPageToken,
  }))
  .then(pageInfo => pageInfo);

const extractData = (clip) => {
  const { id } = clip;
  const {
    title,
    description,
    channelTitle,
    publishedAt,
    thumbnails,
  } = clip.snippet;
  const { viewCount } = clip.statistics;

  const link = `${WATCH_URL}${id}`;

  return {
    link,
    title,
    description,
    channelTitle,
    publishedAt,
    viewCount,
    thumbnails: thumbnails.medium,
  };
};

const fetchChunkVideos = ({ videoIds, ...rest }) => fetch(`${VIDEOS_URL}&id=${videoIds}&part=snippet,statistics`)
  .then(responce => responce.json())
  .then(({ items }) => ({
    items: items.map(extractData),
    ...rest,
  }));

const fetchVideoItems = searchQuery => fetchChunkInfo(searchQuery)
  .then(fetchChunkVideos);

export default fetchVideoItems;
