class PageControl {
  constructor(onPrevClick, onNextClick, currentPageNum) {
    this.onPrevClick = onPrevClick;
    this.onNextClick = onNextClick;
    this.currentPageNum = currentPageNum;
    this.control = null;
  }

  render() {
    const control = document.createElement('div');
    const prev = document.createElement('div');
    const current = document.createElement('div');
    const next = document.createElement('div');
    prev.classList.add('nav');
    next.classList.add('nav');
    control.appendChild(prev);
    control.appendChild(current);
    control.appendChild(next);

    prev.addEventListener('click', this.onPrevClick);
    prev.addEventListener('mousedown', () => {
      const tooltip = document.createElement('div');
      tooltip.classList.add('tooltip');
      tooltip.innerText = this.currentPageNum;
      prev.appendChild(tooltip);
    });
    prev.addEventListener('mouseup', () => {
      prev.innerHTML = '<';
    });
    prev.innerText = '<';
    next.addEventListener('click', this.onNextClick);
    next.addEventListener('mousedown', () => {
      const tooltip = document.createElement('div');
      tooltip.classList.add('tooltip');
      tooltip.innerText = this.currentPageNum + 2;
      next.appendChild(tooltip);
    });
    next.addEventListener('mouseup', () => {
      next.innerHTML = '>';
    });
    next.innerText = '>';
    current.innerText = this.currentPageNum + 1;
    this.current = current;

    return control;
  }

  updateCurPageNum(newPageNum) {
    this.currentPageNum = newPageNum;
    this.current.innerText = this.currentPageNum + 1;
  }
}

export default PageControl;
