class VideoClips {
  constructor(loadPrevPage, loadNextPage, model) {
    this.model = model;
    this.loadPrevPage = loadPrevPage;
    this.loadNextPage = loadNextPage;
    this.mouseMove = this.mouseMove.bind(this);
    this.touchMove = this.touchMove.bind(this);
  }

  renderClipCards() {
    this.content.innerHTML = '';
    this.model.forEach((item) => {
      const clip = document.createElement('div');
      clip.classList.add('clip-card');

      const title = document.createElement('div');
      title.innerHTML = `<a href=${item.link} class=link target=_blank>${item.title}</a>`;
      title.style.backgroundImage = `url(${item.thumbnails.url})`;
      title.classList.add('title');
      clip.appendChild(title);

      const channel = document.createElement('p');
      channel.innerHTML = item.channelTitle;
      clip.appendChild(channel);

      const date = document.createElement('p');
      date.innerHTML = item.publishedAt.slice(0, 10).split('-').reverse().join('-');
      clip.appendChild(date);

      const views = document.createElement('p');
      views.innerHTML = item.viewCount;
      clip.appendChild(views);

      const desc = document.createElement('p');
      desc.innerHTML = item.description;
      desc.classList.add('description');
      clip.appendChild(desc);

      this.content.appendChild(clip);
    });
  }

  mouseMove(event) {
    const offset = event.pageX - this.x;
    this.content.style.left = `${offset}px`;
  }

  touchMove({ changedTouches: [{ pageX }] }) {
    const offset = pageX - this.x;
    this.content.style.left = `${offset}px`;
  }

  render() {
    const container = document.createElement('div');
    container.classList.add('page-cont');
    const content = document.createElement('div');
    content.classList.add('page');
    this.content = content;

    this.renderClipCards();

    container.addEventListener('mousedown', (event) => {
      this.x = event.pageX;
      container.addEventListener('mousemove', this.mouseMove);
    });
    container.addEventListener('mouseup', (event) => {
      this.content.style.left = 0;
      container.removeEventListener('mousemove', this.mouseMove);
      const offset = event.pageX - this.x;
      if (offset === 0) return;
      if (offset < 0) this.loadNextPage();
      else this.loadPrevPage();
    });

    container.addEventListener('touchstart', ({ changedTouches: [{ pageX }] }) => {
      this.x = pageX;
      container.addEventListener('touchmove', this.touchMove);
    });
    container.addEventListener('touchend', ({ changedTouches: [{ pageX }] }) => {
      this.content.style.left = 0;
      container.removeEventListener('mousemove', this.mouseMove);
      const offset = pageX - this.x;
      if (offset === 0) return;
      if (offset < 0) this.loadNextPage();
      else this.loadPrevPage();
    });

    container.appendChild(this.content);
    return container;
  }

  updateClips(clips, isNext = true) {
    const animationClass = isNext ? 'page-move-next' : 'page-move-prev';
    setTimeout(() => {
      this.model = clips;
      this.renderClipCards();
      this.content.classList.add(animationClass);
    });

    this.content.classList.remove('page-move-next', 'page-move-prev');
  }
}

export default VideoClips;
